@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0a0a0b;
}

code {
  font-family: 'Space Grotesk', monospace;
}

@layer base {
  :root {
    --background: 210 24% 6%;
    --foreground: 210 20% 98%;
    --card: 210 24% 8%;
    --card-foreground: 210 20% 98%;
    --popover: 210 24% 8%;
    --popover-foreground: 210 20% 98%;
    --primary: 168 76% 42%;
    --primary-foreground: 0 0% 100%;
    --secondary: 210 20% 16%;
    --secondary-foreground: 210 20% 98%;
    --muted: 210 20% 16%;
    --muted-foreground: 210 20% 60%;
    --accent: 168 76% 42%;
    --accent-foreground: 0 0% 100%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 210 20% 20%;
    --input: 210 20% 20%;
    --ring: 168 76% 42%;
    --radius: 0.75rem;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #0a0a0b;
}

::-webkit-scrollbar-thumb {
  background: #14b8a6;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #0d9488;
}

/* Selection */
::selection {
  background-color: #14b8a6;
  color: #ffffff;
}

/* Smooth Scroll */
html {
  scroll-behavior: smooth;
}

/* Glass Morphism */
.glass {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.glass-dark {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

/* Gradient Text */
.gradient-text {
  background: linear-gradient(135deg, #14b8a6 0%, #06b6d4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Animated Gradient Background */
@keyframes gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animated-gradient {
  background: linear-gradient(270deg, #14b8a6, #0d9488, #14b8a6);
  background-size: 200% 200%;
  animation: gradient-shift 8s ease infinite;
}

/* Floating Animation */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

.float {
  animation: float 6s ease-in-out infinite;
}

/* Fade In Up */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-up {
  animation: fadeInUp 0.8s ease-out forwards;
}

/* Scale In */
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.scale-in {
  animation: scaleIn 0.6s ease-out forwards;
}

/* Glow Effect */
.glow {
  box-shadow: 0 0 20px rgba(20, 184, 166, 0.3),
              0 0 40px rgba(20, 184, 166, 0.2),
              0 0 60px rgba(20, 184, 166, 0.1);
}

.glow-hover:hover {
  box-shadow: 0 0 30px rgba(20, 184, 166, 0.4),
              0 0 60px rgba(20, 184, 166, 0.3),
              0 0 90px rgba(20, 184, 166, 0.2);
  transition: box-shadow 0.3s ease;
}

/* Noise Texture */
.noise {
  position: relative;
}

.noise::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.03;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  pointer-events: none;
}

/* Custom Button Styles */
button {
  font-family: 'Inter', sans-serif;
}

/* Focus States */
input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: #14b8a6;
  box-shadow: 0 0 0 3px rgba(20, 184, 166, 0.1);
}

/* Smooth Transitions */
a, button, .transition-smooth {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Clip Path Sections */
.clip-diagonal {
  clip-path: polygon(0 0, 100% 0, 100% 95%, 0 100%);
}

.clip-diagonal-reverse {
  clip-path: polygon(0 5%, 100% 0, 100% 100%, 0 100%);
}